#banner {
    background-color: #ddb892;
    width: 25vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    position: fixed; /* Use fixed positioning */
    right: 4vw;
    z-index: 2;
}

.links {
    text-decoration: none;
}

.links:visited {
    color: #ddb892; /* Darker brown color */
}

.social-icons {
    font-family: 'Patrick_Hand';
}

