#backdrop {
    background-color: #ddb892;
    position:absolute;
    z-index: -1;
    width: 45vw;
    height: 100vh;
}

#bannerBody {
    // padding: 250px 100px 100px 50px;
    padding: 5vw;
    margin-top: 10vh;
    background-color: #ddb892;
    z-index: 2;
    // align-items: center;
    // position: relative;
    // display: flex;
    // flex-direction: column;
}

.bannerText {
    font-size: 1.5em;
    font-family: 'coffeecake';
    color: #9c6644;
    margin-left:5vw;
    text-align: right;
    margin-bottom:0;
}

.animatedBannerText{
    font-size: 1.5em;
    font-family: 'coffeecake';
    color: #9c6644;
    margin-left:5vw;
    margin-top:3vh;
    text-align: right;
}

#contactButton {
    // padding: 10px 0px 10px 0px;
    padding: 1vw;
    font-size: 1em;
    font-family: 'coffeecake';
    color: #9c6644;
    transition: background-color 0.3s ease;
    position: absolute;
    border-radius: 2vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 15vw;
    margin-top:10vh;
    margin-left: 0;
    margin-right: auto;
}

#contactButton:hover {
    border-radius: 2vw;
    border: 2px solid #9c6644;
    background-color: #9c6644; /* Match the theme color */
    color: #ddb892;
}

#view-resume-link {
    width: 10vw;
    height: 27vh;
    position: absolute;
    bottom: 0vh;
    right:5vw;
    z-index:20; /* Adjust as needed */
    // right: 0;
}

#resumeIcon {
    position: absolute;
    width: 40vw;
    height: 50vh;
    display: flex;
    bottom: -12vh; /* Adjust as needed */
    right: -10vw; /* Adjust as needed */
    z-index: 1;
}

#resume-link {
    text-decoration: none;
}

#resume-button {
    font-family: 'Patrick_Hand', cursive;
    margin-left:7vw;
    color: #ddb892; /* Match the theme color */
    text-decoration: none;
    font-size: 0.6em;
    border: 3px solid #ddb892; /* Match the theme color */
    border-radius: 0.8em;
    transition: background-color 0.3s, color 0.3s, border-color 0.3s;
    cursor: pointer;
    background-color: transparent;
    margin-bottom:1vh;
}

#resume-button:hover {
    background-color: #ddb892; /* Match the theme color */
    color: #ede0d4;
}

